.App-logo {
  animation: spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
  text-align: center;
}

.App-intro {
  font-size: large;
}

.mcq-error {
  text-align: center;
}

.mcq-form {
  padding: 1em;
  background: #f9f9f9;
  border: 1px solid #c1c1c1;
  margin-top: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  padding: 1em;
}

.mcq-input {
  margin-bottom: 1rem;
  background: #fff;
  border: 1px solid #9c9c9c;
}

.mcq-button {
  background: lightgrey;
  padding: 0.7em;
  border: 0;
}

.mcq-button:hover {
  background: gold;
}

.mcq-label {
  text-align: right;
  display: block;
  padding: 0.5em 1.5em 0.5em 0;
}

.mcq-input {
  width: 100%;
  padding: 0.7em;
  margin-bottom: 0.5rem;
}

.mcq-input:focus {
  outline: 3px solid gold;
}

@media (min-width: 400px) {
  .mcq-form {
    overflow: hidden;
  }

  .mcq-label {
    float: left;
    width: 200px;
  }

  .mcq-input {
    float: left;
    width: calc(100% - 200px);
  }

  .mcq-button {
    float: right;
    width: calc(100% - 200px);
  }
}



@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}